	export default {
		data() {
			return {
				tabList: ['快递配送', '上门自提'],
        tabDefault: 0,                     // 快递或者自提  
        shop_id: 0,                        // 自提门店ID
        option: {},                        // 接受到的参数
        delivery: 10,                      // 快递10 自提20
        detail: {},                        // 订单数据
        remark: '',                        // 买家留言
        cart_ids: '',                      // 购物车进来传入id
        isCart: false,                     // 是否购物车进来
			};
		},
    onLoad(option) {
      console.log(option)
      if(option.data) {
        this.option= JSON.parse(option.data)
        console.log('data')
        return
      }
      if(option.cart === 'true') {
        console.log('cart')
        this.isCart= true
        this.cart_ids= option.cart_ids
      }
    },
    onShow() {
      // 提交订单
      this.orderBuy()
    },
    watch: {
      tabDefault(val, oldval) {
        if(val === 1) {
          this.delivery= 20
        } else {
          this.delivery= 10
        }
        this.orderBuy()
      }
    },
    methods: {
      selectTab(index) {
        this.tabDefault= index
      },
      // 提交订单
      orderBuy() {
        let that = this
        let data = that.option
        let url= that.$api.orderBuyNow
        data.delivery= that.delivery
        if(that.isCart) {
          url= that.$api.orderCart
          data= {
            cart_ids: that.cart_ids,
            shop_id: that.shop_id,
            delivery:that.delivery
          }
        }
        that.$http({
          url: url,
          data: data,
          cb: (err, res) => {
            if(!err && res.code === 1) {
              that.detail= res.data
              if(res.data.extract_shop) {
                this.shop_id= res.data.extract_shop.shop_id
              }
            } else if(res.code === 0 || res.code === -1 && res.msg) {
              uni.showToast({
                title: res.msg,
                icon: 'none'
              })
            } else {
              uni.showToast({
                title: '订单数据生成失败',
                icon: 'none'
              })
            }
          }
        })
      },
      // 选择自提门店
      selectExtractPoint() {
        uni.chooseLocation({
          success: function (res) {
            console.log('位置名称：' + res.name);
            console.log('详细地址：' + res.address);
            console.log('纬度：' + res.latitude);
            console.log('经度：' + res.longitude);
            
          }
        });
      },
      // 选择收货地址
      selectAddress() {
        uni.navigateTo({
          url: '../mine/address'
        })
      },
      goDetail(item) {
        uni.navigateTo({
          url: '../components/goodDetail?goods_id=' + this.option.goods_id + '&panic=true' + '&title=' + this.option.title
        })
      }
    }
	}
